  box-sizing: border-box;
}

.splash .message {
  font-size: 72px;
  line-height: 72px;
  //color: rgb(222, 222, 212);
  text-shadow: black 0 0 5px;
  //text-transform: uppercase;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.splash .fa {
  text-align: center;
  display: inline-block;
  font-size: 72px;
  margin-top: 50px;
}

