/* Base structure */
body { margin: 0; overflow-x: hidden; }

@media only screen and (max-width: 768px) { .page-host { top: 149px; } }
.splash { text-align: center; margin: 10% 0 0 0; box-sizing: border-box; }

.splash .message { font-size: 72px; line-height: 72px; text-shadow: black 0 0 5px; font-family: "Helvetica Neue", Helvetica, Arial, sans-serif; }

.splash .fa { text-align: center; display: inline-block; font-size: 72px; margin-top: 50px; }

sup > i.required-asterisk { color: #ff0000; font-size: 8px; }

router-view { display: block; }

.popover .popover-content > div { display: block !important; }

.row-fluid { white-space: nowrap; overflow-x: auto; }

.row-fluid > div { display: inline-block; }


